import React from "react";
import { CheckIcon } from "lucide-react";

function Progress({ cnsDetails }) {
  const stages = ["Booked", "In Transit", "Arrived", "Delivered"];
  const progress = ["0%", "30%", "60%", "90%"];
  const { status } = cnsDetails;
  const currentStageIndex = stages.indexOf(status);
  const progressPercentage = progress[currentStageIndex];

  return (
    <div className="w-full max-w-4xl mx-auto px-4 py-8">
      {/* Titles */}
      <div className="flex justify-between mb-2">
        {stages.map((stage) => (
          <div
            key={stage}
            className="text-center w-24 text-lg text-[#003262] font-bold"
          >
            {stage}
          </div>
        ))}
      </div>

      {/* Progress Bar */}
      <div className="relative flex items-center justify-between">
        {/* Background line */}
        <div className="absolute top-1/2 left-12 right-12 h-1 bg-gray-300 z-0" />

        {/* Filled progress */}
        <div
          className="absolute top-1/2 left-12 right-12 h-1 bg-[#003262] z-10 transition-all duration-300"
          style={{ width: progressPercentage }}
        />

        {/* Icons */}
        {stages.map((stage, idx) => (
          <div key={stage} className="relative z-20 w-24 flex justify-center">
            <div
              className={`w-8 h-8 rounded-full flex items-center justify-center ${
                currentStageIndex >= idx
                  ? "bg-[#003262] text-white"
                  : "bg-gray-300 text-[#003262]"
              }`}
            >
              {currentStageIndex >= idx ? (
                <CheckIcon className="w-5 h-5" />
              ) : (
                idx + 1
              )}
            </div>
          </div>
        ))}
      </div>

      {/* Dates */}
      <div className="flex justify-between mt-2 text-sm text-[#003262] font-semibold">
        <div className="text-center w-24">{cnsDetails.bookingDate || ""}</div>
        <div className="text-center w-24"></div>
        <div className="text-center w-24">{cnsDetails.arrivedDate || ""}</div>
        <div className="text-center w-24">{cnsDetails.deliveryDate || ""}</div>
      </div>
    </div>
  );
}

export default Progress;
