import { useEffect, useState } from "react";
import Progress from "./Progress";

function CNS_Tracker() {
  const [lr, setLr] = useState("");
  const [transport, setTransport] = useState("TCI");

  const [cnsDetails, setCnsDetails] = useState({
    bookingDate: "",
    bookingFrom: "",
    deliveryTo: "",
    packages: "",
    arrivedDate: "",
    deliveryDate: "",
    transitDetails: [],
    status: "",
  });

  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");
  const [isHidden, setIsHidden] = useState(true);

  const location = window.location;
  const queryParams = new URLSearchParams(location.search);
  const lrFromUrl = queryParams.get("lr");
  const transportFromUrl = queryParams.get("transport");

  const fetchTCITrackingData = async (docket) => {
    const url =
      "https://atlog.tcil.in/TLOG/WebServices/TLOG_API/API/ShipmentTrackingList";
    const options = {
      method: "POST",
      headers: { "content-type": "application/json" },
      body: JSON.stringify({ CnsNo: docket, DivisionCode: "1" }),
    };

    const response = await fetch(url, options).then((res) => res.json());
    if (!response.Error) {
      const bookingDetails = response.shipmentTrackingS1_DATAs[0];
      const transitDetails = response.shipmentTrackingS4_DATAs.map((obj) => {
        const [month, day, yearTime] = obj.LIST_ORDER.split("/");
        const [year, time] = yearTime.split(" ");
        const [hours, minutes, seconds] = time.split(":");
        const date = new Date(year, month - 1, day, hours, minutes, seconds);

        const formattedDate = date
          .toLocaleString("en-GB", {
            day: "2-digit",
            month: "2-digit",
            year: "numeric",
            hour: "2-digit",
            minute: "2-digit",
            hour12: true,
          })
          .replace(",", "");

        const event = obj.STATUS.replace(/^[(]|[)]$/g, "");

        return {
          date: formattedDate,
          city: obj.CITY,
          event,
        };
      });

      const bookingDate = bookingDetails.BKG_DATE;
      const bookingFrom = bookingDetails.BKG_BRN;
      const deliveryTo = bookingDetails.DLY_BRN;
      const packages = bookingDetails.TOT_PKGS;
      const arrivedDate = bookingDetails.ARR_DATE;
      const deliveryDate = bookingDetails.DLY_DATE;
      const status = deliveryDate
        ? "Delivered"
        : arrivedDate
        ? "Arrived"
        : "In Transit";

      setCnsDetails({
        bookingDate,
        bookingFrom,
        deliveryTo,
        packages,
        arrivedDate,
        deliveryDate,
        transitDetails,
        status,
      });
    } else {
      throw new Error(response.Error.Status_Message);
    }
  };

  const fetchVRLTrackingData = async (docket) => {
    const url = `https://www.vrlgroup.in/track_consignment.aspx?lrtrack=1&lrno=${docket}`;
    const options = { method: "POST" };

    const response = await fetch(url, options).then((res) => res.json());
    if (response.Status === "Success") {
      const shipment = response.shipment[0];
      const transitDetails = shipment.track.map((obj) => {
        const [day, month, year] = obj.scanDate.split("-");
        const monthMap = {
          Jan: 0,
          Feb: 1,
          Mar: 2,
          Apr: 3,
          May: 4,
          Jun: 5,
          Jul: 6,
          Aug: 7,
          Sep: 8,
          Oct: 9,
          Nov: 10,
          Dec: 11,
        };
        const [hours, minutes] = obj.scanTime.split(":");
        const date = new Date(year, monthMap[month], day, hours, minutes);

        const formattedDate = date
          .toLocaleString("en-GB", {
            day: "2-digit",
            month: "2-digit",
            year: "numeric",
            hour: "2-digit",
            minute: "2-digit",
            hour12: true,
          })
          .replace(",", "");

        return {
          date: formattedDate,
          city: obj.ldfromcity,
          event: obj.eventDesc,
          status: obj.Status,
        };
      });

      const bookingDate = shipment.shipDate;
      const bookingFrom = shipment.origin;
      const deliveryTo = shipment.bookingtocode;
      const packages = shipment.noa;
      const arrivedDate =
        shipment.track.find(
          (detail) => detail.Status === "ARRIVED AT DESTINATION"
        )?.scanDate || "";

      console.log(arrivedDate);
      const deliveryDate = shipment.deliveryDate;
      const status = (() => {
        switch (transitDetails[0].status) {
          case "IN-TRANSIT":
            return "In Transit";
          case "DELIVERED":
            return "Delivered";
          case "ARRIVED AT DESTINATION":
            return "Arrived";
          default:
            return "Booked";
        }
      })();

      setCnsDetails({
        bookingDate,
        bookingFrom,
        deliveryTo,
        packages,
        arrivedDate,
        deliveryDate,
        transitDetails,
        status,
      });
    } else {
      throw new Error("");
    }
  };

  const track = async (transport, docket) => {
    if (isLoading && transport) return;
    setIsLoading(true);
    setIsHidden(true);
    setError("");

    try {
      if (transport === "TCI") {
        await fetchTCITrackingData(docket);
      } else {
        await fetchVRLTrackingData(docket);
      }
      setIsHidden(false);
    } catch (error) {
      console.error("Error fetching tracking data:", error);
      setError(`${error}`);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (transportFromUrl && lrFromUrl) {
      setTransport(transportFromUrl);
      setLr(lrFromUrl);
      track(transportFromUrl, lrFromUrl);
    }
  }, []);

  const onLrChange = (e) => {
    setLr(e.target.value);
  };

  const onTransportChange = (e) => {
    setTransport(e.target.value);
    console.log(e.target.value);
  };

  return (
    <div className="App flex min-h-screen min-w-screen bg-[#F8F8FF] justify-center items-center">
      <div
        id="content-container"
        className="flex flex-col h-full justify-center items-center px-2 py-48"
      >
        <div
          id="inputContainer"
          className="flex bg-[#003262] w-fit rounded-full items-center mt-2 mb-16 p-2 drop-shadow-lg"
        >
          <div className="bg-[#F8F8FF] rounded-full">
            <select
              name="transport"
              id="selectTransport"
              value={transport}
              onChange={onTransportChange}
              className="bg-[#F8F8FF] w-fit text-sm md:text-xl h-full rounded-s-full pl-2 py-2"
            >
              <option value="TCI">TCI Freight</option>
              <option value="VRL">VRL Logistics</option>
            </select>
            <input
              type="text"
              className="bg-[#F8F8FF] max-w-36 md:max-w-none text-sm md:text-2xl rounded-e-full border-s px-4 py-2"
              placeholder="Enter LR No."
              value={lr}
              onChange={onLrChange}
            />
          </div>
          <button
            className="text-base md:text-xl text-white mx-4"
            onClick={() => track(transport, lr)}
            disabled={isLoading}
          >
            Search
          </button>
        </div>
        {error && (
          <div className="bg-red-50 p-2 border border-red-500 rounded-xl">
            <div className="text-red-500">
              {error}
              <br />
              Failed to get tracking info. Please try again later.
            </div>
          </div>
        )}
        {isLoading && (
          <div className="">
            <dotlottie-player
              src="https://lottie.host/ac81aad1-3549-4591-a393-d1db5d1af52c/d7VVMJSK8z.json"
              background="transparent"
              speed="1"
              style={{ width: 64, height: 64 }}
              loop
              autoplay
            ></dotlottie-player>
          </div>
        )}
        {!isHidden && !isLoading && (
          <>
            {/* Booking Details */}
            <div
              id="bookingDtlContainer"
              className="flex flex-wrap w-full mt-2 text-left text-md md:text-xl text-[#003262] justify-around"
            >
              <div className="mb-4 mx-4 min-w-32">
                <div className="font-bold">Booking From:</div>
                {cnsDetails.bookingFrom}
              </div>
              <div className="mb-4 mx-4 min-w-32">
                <div className="font-bold">Delivery To:</div>
                {cnsDetails.deliveryTo}
              </div>
              <div className="mb-4 mx-4 min-w-32">
                <div className="font-bold">Packages:</div>
                {cnsDetails.packages}
              </div>
            </div>

            {/* Delivery Status */}
            <Progress cnsDetails={cnsDetails} />

            {/* Transit Details */}
            <div className="bg-[#F8F8FF] p-3 m-2 mt-4 md:m-16 rounded-3xl border border-[#003262] drop-shadow-xl">
              <table className="table-auto min-w-[700px] md:text-xl text-[#003262] text-left">
                <thead>
                  <tr>
                    <th className="md:px-4 py-1 border-r border-b border-[#003262]">
                      DATE
                    </th>
                    <th className="md:px-4 pl-2 py-1 border-b border-[#003262]">
                      STATUS
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {cnsDetails.transitDetails.map((transit, index) => (
                    <tr key={index}>
                      <td className="md:px-4 py-3 border-r border-[#003262]">
                        {transit.date}
                      </td>
                      <td className="pl-2 md:px-4 py-3 pre-wrap">
                        {transit.city}
                        <br />
                        {transit.event}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default CNS_Tracker;
